.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.title-wrapper {
  display: flex;
  align-content: center;
  align-items: center;
}

.pulse {
  background: rgb(232, 19, 19);
  border: 1px solid black;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  margin: 10px;
  box-shadow: 0 0 0 0 rgba(232, 19, 19, 1);
  transform: scale(1);
  animation: pulsate 2s infinite;
}

.map-container {
  position: relative;
}

.pulse.on-map.own {
  height: 15px;
  position: absolute;
  width: 15px;
}

.pulse.on-map.shared {
  background: rgb(84, 152, 224);
  box-shadow: 0 0 0 0 rgba(84, 152, 224, 1);
  height: 15px;
  position: absolute;
  width: 15px;
  animation: pulsate-shared 2s infinite;
}

.map {
  width: 100vw;
  max-width: 400px;
}

@keyframes pulsate {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(232, 19, 19, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 25px rgba(232, 19, 19, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(232, 19, 19, 0);
  }
}

@keyframes pulsate-gray {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 gray;
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 25px rgba(232, 19, 19, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(232, 19, 19, 0);
  }
}

@keyframes pulsate-shared {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(84, 152, 224, 1);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 25px rgba(232, 19, 19, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(232, 19, 19, 0);
  }
}

.share-button {
  background-color: rgb(84, 152, 224);
  border: white solid 2px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 25px;
  height: 50px;
  margin-bottom: 20px;
  margin-top: 20px;
  width: 200px;
}
